<template>
	<div id="app">
		<div class="start" :class="{active:!showStart}" @click="showStart = false">
			<div class="start_word">
				<div class="inner" style="margin:0 80px;">
					<div style="background: rgba(255,255,255,0z);width: 200px;margin: 0 auto;color:#ffc30e;">
						<div class="" style="font-size: 103px;margin-bottom: -20px;">当</div>
						<div class="" style="font-size: 50px;margin-bottom: -7px;">工作</div>
						<div class="" style="font-size: 33px;">正好是</div>
						<div class="" style="font-size: 20px;padding-bottom: 20px;">爱好的时候</div>
					</div>
					<div class="loading">
						<span>.</span>
						<span>.</span>
						<span>.</span>
					</div>
					<div class="sm_word" style="padding-top: 30px;">再没有比这更棒的事情了</div>
					<div style="font-size: 12px;color: rgba(255,255,255,0.6);margin-top: 10px;">可以点击页面关闭动画</div>
				</div>
			</div>
			<video :src="`./static/fields.mp4`" autoplay="autoplay" loop="loop" muted ref="video"></video>
		</div>
		<div class="nav">
			<div class="logo">
				<img :src="require(`@/assets/logo.png`)" alt="">
			</div>
			<ul>
				<li v-for="(nav, index) in navs" :class="{'active':curNavIndex===index}" @click="changePage(index)">
					<img :src="changeNavImg(index,nav)" alt="">
				</li>
			</ul>
		</div>
		<div class="main">
			<router-view />
		</div>
	</div>
</template>

<script>
	export default {
		name: 'App',
		data() {
			return {
				showStart: true,
				timeout: null,
				curNavIndex: 0,
				navs: [{
						normal: require(`@/assets/nav_1.png`),
						active: require(`@/assets/nav_1_active.png`)
					},
					{
						normal: require(`@/assets/nav_2.png`),
						active: require(`@/assets/nav_2_active.png`)
					},
					{
						normal: require(`@/assets/nav_3.png`),
						active: require(`@/assets/nav_3_active.png`)
					},
					{
						normal: require(`@/assets/nav_4.png`),
						active: require(`@/assets/nav_4_active.png`)
					}
				]
			}
		},
		methods: {
			// 改变导航图片
			changeNavImg(index, nav) {
				if (this.curNavIndex === index) {
					return nav.active
				} else {
					return nav.normal
				}
			},
			// 跳转页面
			changePage(index) {
				this.curNavIndex = index
				let pageName = 'index'
				switch (index) {
					case 0:
						pageName = 'index'
						break;
					case 1:
						pageName = 'intro'
						break;
					case 2:
						pageName = 'pic'
						break;
					case 3:
						pageName = 'web'
						break;
					default:
						break;
				}

				this.$router.push({
					name: pageName
				})
			}
		},
		watch: {
			$route(to, from) {
				switch (to.name) {
					case 'index':
						this.curNavIndex = 0
						break;
					case 'intro':
						this.curNavIndex = 1
						break;
					case 'pic':
						this.curNavIndex = 2
						break;
					case 'web':
						this.curNavIndex = 3
						break;
					default:
						break;
				}
			}
		},
		destroyed() {
			clearTimeout(this.timeout)
		},
		mounted() {
			this.$refs.video.currentTime = 5
			this.$refs.video.addEventListener("canplaythrough", () => { //监听audio是否加载完毕，如果加载完毕，则读取audio播放时间
				this.$refs.video.play()
				this.timeout = setTimeout(() => {
					this.showStart = false
				}, 3000)
			})
		}
	}
</script>

<style scoped lang="less">
	@import "./common/less/index.less";

	@nav_width: 70px;

	.nav {
		position: fixed;
		left: 0;
		top: 0;
		width: @nav_width;
		height: 100vh;
		background: #2a2a2a;
		z-index: 2;

		ul {
			height: calc(100vh - @nav_width);


			li {
				text-align: center;
				padding: 30px 0;
				cursor: pointer;
				transition: all 0.5s;

				&:hover,
				&:active {
					background: #000;
				}

				img {
					vertical-align: bottom;
					width: 35px;
				}
			}
		}
	}

	.logo {
		background: @theme_color_1;
		width: @nav_width;
		height: @nav_width;
		margin-bottom: 30px;
		text-align: center;

		img {
			width: 90%;
			vertical-align: bottom;
		}
	}

	.main {
		width: calc(100vw - @nav_width);
		margin-left: @nav_width;
	}

	.start {
		width: 100vw;
		height: 100vh;
		position: fixed;
		background: linear-gradient(0deg, #1f2636, #606c88);
		top: 0;
		left: 0;
		z-index: 10;
		animation:bgColor 3s infinite;
		-moz-animation:bgColor 3s infinite; /* Firefox */
		-webkit-animation:bgColor 3s infinite; /* Safari and Chrome */
		-o-animation:bgColor 3s infinite; /* Opera */
	}

	.start,
	.start img,
	.start_word {
		transition: all 1s;
	}

	.start img {
		position: fixed;
		width: 100vw;
		height: 100vh;
		top: 0;
		left: 0;
		z-index: 9;
	}

	.start video {
		position: fixed;
		width: 130vw;
		// height: 100vh;
		top: -200px;
		left: 0;
		z-index: 9;
	}

	.start.active {
		opacity: 0;
		left: -100vw;
		
		video{
			left: -130vw;
		}
		.start_word {
			margin-left: -100%;
			opacity: 0;
		}
	}

	.start_word {
		position: fixed;
		width: 100vw;
		top: 0;
		left: 0;
		z-index: 10;
		font-size: 30px;
		color: #e6e6e6;
		text-align: center;
		padding: 20vh 0;
	}

	@media screen and (max-width: 768px) {
		@nav_width: 40px;

		.nav {
			width: @nav_width;

			ul {
				li {
					img {
						width: 25px;
					}
				}
			}
		}

		.logo {
			width: @nav_width;
			height: @nav_width;
		}

		.main {
			width: calc(100vw - @nav_width);
			margin-left: @nav_width;
		}

		.start video {
			width: auto;
			height: 130vh;
			left: -200vw;
		}
		.start.active video {
			height: 0;
		}
		.start_word{
			padding: 15vh 0;
		}
	}
	.loading span:nth-child(1){
		animation:load 0.5s infinite;
		-moz-animation:load 0.5s infinite; /* Firefox */
		-webkit-animation:load 0.5s infinite; /* Safari and Chrome */
		-o-animation:load 0.5s infinite; /* Opera */
	}
	.loading span:nth-child(2){
		animation:load 1s infinite;
		-moz-animation:load 1s infinite; /* Firefox */
		-webkit-animation:load 1s infinite; /* Safari and Chrome */
		-o-animation:load 1s infinite; /* Opera */
	}
	.loading span:nth-child(3){
		animation:load 1.5s infinite;
		-moz-animation:load 1.5s infinite; /* Firefox */
		-webkit-animation:load 1.5s infinite; /* Safari and Chrome */
		-o-animation:load 1.5s infinite; /* Opera */
	}
	@keyframes load
	{
		from {
			opacity: 0;
		}
		to{
			opacity: 1;
		}
	}
	@keyframes bgColor
	{
		0% {
			background: linear-gradient(0deg, #606c88, #1f2636);
		}
		50%{
			background: linear-gradient(0deg, #8c747b, #1D2B64);
		}
		100% {
			background: linear-gradient(0deg, #606c88, #1f2636);
		}
	}
</style>



